import React, { Component } from 'react'

import './style1.css' //Main CSS

// import Big Post
import Firstpost from './firstpost'
import Example1 from './CatPost'; // Data for CatPost grid

import { Card } from 'react-bootstrap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPaw } from '@fortawesome/free-solid-svg-icons'
import { faClock, faCommentAlt } from '@fortawesome/free-regular-svg-icons'


// Add Slider
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";


// Import all JSON data
import data from "./Catsnew.json";
const socialMediaList = data.Feuil1;






export default class Post extends Component {
    render() {

        const postData = () =>
            socialMediaList.map(s => (


                <div key={s.ID} className={`cardId${s.ID} cardId card_full`} style={{ backgroundImage: "url("+s.Image+")" }} viewportWidth="400px" cellPadding={ 5 }>
                    <div className="card-body">
                        <div className="post_categories"><a href="#" alt={s.Titre}>{s.Catégories}</a></div>
                        <div className="post_title">{s.Titre}</div>
                        <div>
                            <FontAwesomeIcon icon={faClock} />{s.Readtime} <FontAwesomeIcon icon={faCommentAlt} />{s.Commentaires}
                        </div>

                    </div>
                </div>



            ));

        return (
            <div className="container">

                <h3 className="articles_header">New articles</h3>
                <h1>Our publications <FontAwesomeIcon icon={faPaw} /></h1>
                <Firstpost />


                <Slider
                    dots={false}
                    slidesToShow={3}
                    slidesToScroll={3}
                    autoplay={true}
                    autoplaySpeed={5000}
                    gap={'10px'}
                    cover={true}
                    isThumbs={true}
                    arrows={true}
                    fixedWidth={110}
                    fixedHeight={70}
                >
                    {postData()}

                </Slider>
                {/* <Example1 /> */}


            </div>
        )
    }
}
